import React from 'react'
import { ActivityIndicator } from 'antd-mobile'

const style = { textAlign: 'center' }

export default function renderEmpty(loader, options) {
  if (loader.isEmpty) {
    if (loader.isLoading) {
      return <div style={style}><ActivityIndicator text="..." /></div>
    } else if (loader.error) {
      return <div style={style}>!{loader.error.message}<span onClick={() => loader.refresh()}></span></div>
    } else {
      return <div style={style}>,<span onClick={() => loader.refresh()}></span></div>
    }
  }
}